<template>
  <div id="login-select">
    <h2 class="title">店铺选择</h2>

      <ul class="shop-content clearfix">
        <li 
        class="shop-item left" 
        v-for="(item, index) in shoplist" 
        :key="item.shop_id"
        @click="login(item.shop_id)"
        >
          <div class="logo-img">
            <img :src="imgbase_url + '/img_get.php?img=1&height=69&width=69&imgname=' + item.shop_logo" alt="店铺logo">
          </div>
          <span class="shop-name">{{ item.shop_name}}</span>
        </li>
      </ul>

    </div>
  
</template>

<script>
import { loginShop } from "../api";
import { Public } from "@/config/public";
import { ls } from "@/config/pageStore";

export default {
  data() {
    return {
      shoplist: [
        {
          shop_id: "4",
          shop_name: "金拱门",
          shop_logo: "b831c63e8bbb9ed9af2f878d09ce8be9.jpg"
        },
        {
          shop_id: "5",
          shop_name: "金拱2",
          shop_logo: "b831c63e8bbb9ed9af2f878d09ce8be9.jpg"
        }
      ],
      imgbase_url: imgbase_url
    };
  },
  created() {
    this.init();
  },
  methods: {
    login(id) {
      let data = {
        login_shop: 1,
        shop_id: id
      };
      loginShop(data, resp => {
        if (resp.ret === 0) {
          ls.setItem("public#shopid", id);
          this.$router.push({ path: "/shop" });
        }
      });
    },
    init() {
      this.shoplist = Public.getShopinfo().shopinfo || [];
      
      window.onstorage = v => {
        if (v.key === "public#shopid") {
          this.$notify({
            title: "警告",
            message: "在新标签打开其他店铺",
            type: "warning",
            duration: 0,
            // onClose: () => {
            //   // this.closeWebPage();
            // }
          });
        }
      };
    },
    closeWebPage() {
      if (navigator.userAgent.indexOf("MSIE") > 0) {
        //close IE
        if (navigator.userAgent.indexOf("MSIE 6.0") > 0) {
          window.opener = null;
          window.close();
        } else {
          window.open("", "_top");
          window.top.close();
        }
      } else if (navigator.userAgent.indexOf("Firefox") > 0) {
        //close firefox
        window.location.href = "about:blank ";
      } else {
        //close chrome;It is effective when it is only one.
        window.opener = null;
        window.open("", "_self");
        window.close();
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.title {
  text-align: center;
}
.shop-content {
  padding: 50px 40px;
}

.logo-img {
  @include wh(100px, 100px);
  border-radius: 4px;
  margin-bottom: 10px;
  cursor: pointer;

  img {
    @include wh(100px, 100px);
    border-radius: 4px;
  }
}
.shop-item {
  @include fc(14px, #333);
  text-align: center;
  margin-right: 20px;
}

.shop-name {
  cursor: pointer;
}
</style>



